@import '../../_variables.scss';

$renewYourLookColorAd: #7e2123;

.renewYourLookAd {
  display: flex;
  margin: 50px auto;
  height: 40vh;
  > img {
    width: 60vw;
  }
  &__description {
    position: relative;
    width: 40vw;
    background-color: #e2dcd5;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    &__title {
      font-size: 25px;
      font-weight: 700;
      text-align: center;
      color: $renewYourLookColorAd;
      margin-top: 50px;
    }
    &__val {
      font-size: 13px;
      color: $renewYourLookColorAd;
      margin-top: 20px;
      font-weight: 200;
      text-align: center;
    }
    &__button {
      font-size: 12px;
      width: 50px;
      padding: 5px;
      margin: 20px auto;
      text-align: center;
      color: white;
      background-color: $renewYourLookColorAd;
      border-radius: 8px;
      cursor: pointer;
    }
    &__button:hover {
      border: 2px solid $renewYourLookColorAd;
      color: $renewYourLookColorAd;
      background-color: inherit;
    }
  }
}

@media screen and (min-width: 450px) {
  .renewYourLookAd {
    height: 50vh;
    > img {
      width: 30vw;
    }
    &__description {
      width: 70vw;
      &__title {
        font-size: 45px;
        margin-top: 50px;
      }
      &__val {
        font-size: 25px;
        margin-top: 50px;
      }
      &__button {
        font-size: 33px;
        width: 250px;
        margin: 50px auto;
      }
    }
  }
}
