.componentHeader {
  &__header {
    font-size: 20px;
    text-align: center !important;
    margin-top: 50px;
    > img {
      height: 150px;
      margin: 0;
    }
    > h3 {
      font-size: 19px;
      margin-top: -80px;
    }
  }
}

@media screen and (min-width: 450px) {
  &__header {
    > img {
      height: 200px;
    }
    > h3 {
      margin-top: -100px;
    }
  }
}
