@import '../../_variables.scss';

.loadMoreButton {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 25px auto;
  div {
    width: 150px;
    height: 30px;
    border: 2px solid $loadMoreColor;
    background-color: $loadMoreColor !important;
    color: white;
    transition: background-color 1s ease;
    border-radius: 60px;
    font-size: 17px;
    cursor: pointer;
    background-color: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  div:hover {
    background-color: white !important;
    color: black;
  }
}

@media screen and (min-width: 760px) {
  .loadMoreButton {
    div {
      width: 200px;
      height: 40px;
      font-size: 20px;
    }
  }
}
