@import '../../_variables.scss';

.sendOriginalProductAd {
  display: flex;
  margin: 30px auto;
  height: 40vh;
  > img {
    width: 60vw;
  }
  &__description {
    position: relative;
    width: 40vw;
    background-color: #929254;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    &__title {
      font-size: 25px;
      font-weight: 700;
      text-align: center;
      color: #f4dab2;
      margin-top: 50px;
    }
    &__val {
      font-size: 13px;
      color: #f4dab2;
      margin-top: 20px;
      font-weight: 200;
      text-align: center;
    }
  }
}

@media screen and (min-width: 450px) {
  .sendOriginalProductAd {
    height: 50vh;
    > img {
      width: 30vw;
    }
    &__description {
      width: 70vw;
      &__title {
        font-size: 45px;
        margin-top: 50px;
      }
      &__val {
        font-size: 25px;
        margin-top: 50px;
      }
    }
  }
}
