@import '../../_variables.scss';

.categoryCard {
  padding: 0 10px;
  cursor: pointer;
  color: $loadMoreColor !important;
  position: relative;
  &__badge {
    position: absolute;
    z-index: 5;
    .circularBadge {
      margin-top: 10px;
      width: 20px;
      height: 20px;
      border-radius: 6px;
      background-color: $loadMoreColor;
      display: flex;
      justify-content: center;
      align-items: center;
      > span {
        font-size: 10px;
        color: white;
      }
    }
  }
  &__mainImage {
    overflow: hidden;
    border-radius: 50%;
  }
  &__mainImage > img {
    height: 150px;
    width: 100%;
    transition: transform 0.9s ease-in-out; /* Smooth transition effect */
  }
  &__mainImage:hover > img {
    transform: scale(1.2); /* Zoom in*/
  }
  &__title {
    text-align: center;
    color: $loadMoreColor;
    font-size: 12px;
    font-weight: 700;
    margin-top: 20px;
  }
  &__title:hover {
    color: $hoverColor;
  }
}

@media screen and (min-width: 760px) {
  .categoryCard {
    &__badge {
      .circularBadge {
        margin-top: 15px;
        width: 30px;
        height: 30px;
        border-radius: 10px;
        > span {
          font-size: 12px;
        }
      }
    }
    &__title {
      font-size: 15px;
    }
  }
}
