.categoryProducts {
  padding: 0 10px;

  &__body {
    display: flex;
    flex-wrap: wrap;
  }
  &__item {
    width: 50%;
    margin: 30px 0;
  }
}

.categoryNotFound {
  padding: 20px;
  text-align: center;
  font-size: 30px;
}

@media screen and (min-width: 450px) {
  .categoryProducts {
    &__item {
      width: 25%;
    }
  }
}
