@import '../../_variables.scss';

.shopByBrand {
  &__headerImage {
    > img {
      height: 252px;
      width: 100%;
    }
  }
  &__buttons {
    margin: 15px;
    background-color: #f6f6f6;
    height: 40px;
    .react-slideshow-container {
      padding-left: 30px;
      padding-right: 30px;
    }
  }
  &__button {
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    height: 35px;
    cursor: pointer;
  }
  &__button:hover,
  .selectedBrand {
    color: $hoverColor;
    border: 2px solid $hoverColor;
    border-radius: 20px;
  }

  &__body {
    display: flex;
    flex-wrap: wrap;
  }
  &__item {
    width: 50%;
    margin: 30px 0;
  }
}

@media screen and (min-width: 450px) {
  .shopByBrand {
    &__item {
      width: 25%;
    }
  }
}
